.blueBtn {
  display: inline-block;
  vertical-align: middle;
  padding: 0px 20px;
  height: 30px;
  line-height: 30px;
  background: #03acef;
  color: #fff;
  box-shadow: 1px 1px 4px #a7bac7;
  border-radius: 4px;
  border: 0;
  min-width: 75px;
}
.blueBtn:hover {
  color: white;
}
.blueBtn:active {
  color: white;
}
.blueBtn:visited {
  color: white;
}
.icon-upload {
  display: inline-block;
  vertical-align: middle;
  width: 19px;
  height: 14px;
  background: url("../images/file-upload.png") no-repeat center center;
  margin-top: -5px;
}
.upload-btn {
  display: inline-block;
  vertical-align: middle;
  position: relative;
}
.upload-btn .blueBtn {
  padding: 0 10px;
}
.upload-btn .icon-upload {
  margin-right: 3px;
}
.upload-btn .file {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  font-size: 0;
  opacity: 0;
}
.pop-close {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  background: url("../images/pop-close.png") no-repeat center center;
}
.video-consoleBox {
  position: relative;
  padding: 10px 130px 2px 20px;
  height: auto !important;
}
.video-consoleBox .table-console > li {
  margin-right: 20px;
  margin-bottom: 8px;
}
.video-consoleBox .console-right {
  position: absolute;
  right: 10px;
  top: 10px;
  margin: 0 !important;
}
.video-tableBox {
  padding: 0 20px;
}
.video-tableBox .playBox {
  position: relative;
  width: 130px;
  height: 75px;
  border-radius: 4px;
  overflow: hidden;
}
.video-tableBox .playBox:hover .playBtn {
  opacity: 1;
}
.video-tableBox .playBox img {
  width: 100%;
  height: 100%;
}
.video-tableBox .playBox .playBtn {
  position: absolute;
  left: 50%;
  margin-left: -19px;
  top: 50%;
  margin-top: -19px;
  display: inline-block;
  width: 38px;
  height: 38px;
  background: url("../images/icon-bofang.png") no-repeat center center;
  opacity: 0.8;
}
.video-tableBox .vi-tit a {
  font-size: 18px;
  color: #283e53;
}
.video-tableBox .icon-rentou {
  display: inline-block;
  vertical-align: middle;
  margin: -4px 3px 0 0;
  width: 18px;
  height: 18px;
  background: url("../images/icon-rentou.png") no-repeat center center;
}
.video-tableBox .icon-yanjing {
  display: inline-block;
  vertical-align: middle;
  margin: -4px 3px 0 0;
  width: 20px;
  height: 13px;
  background: url("../images/icon-yanjing.png") no-repeat center center;
}
.video-tableBox .vi-explain {
  margin-top: 15px;
  font-size: 12px;
  color: #9eabbc;
}
.video-tableBox .vi-explain span {
  margin-right: 30px;
}
.video-tableBox .controlTd > a {
  margin-left: 24px;
  color: #3598db;
}
.video-tableBox .controlTd > a:first-child {
  margin-left: 0;
}
.video-tableBox .reportBox {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.video-tableBox .reportBox a {
  color: #3598db;
}
.video-tableBox .reportBox .reportList {
  position: absolute;
  z-index: 5;
  left: -52px;
  top: 30px;
  background: white;
  min-width: 135px;
  padding: 0 10px 10px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  display: none;
}
.video-tableBox .reportBox .reportList:before {
  position: absolute;
  z-index: 2;
  left: 50%;
  margin-left: -8px;
  top: -8px;
  content: '';
  width: 0;
  height: 0;
  border-bottom: 8px solid white;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
}
.video-tableBox .reportBox .reportList:after {
  position: absolute;
  z-index: 1;
  left: 50%;
  margin-left: -8px;
  top: -10px;
  content: '';
  width: 0;
  height: 0;
  border-bottom: 8px solid #ecf0f2;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
}
.video-tableBox .reportBox .reportList > ul > li {
  margin-top: 10px;
  white-space: nowrap;
  color: #283e53;
  text-align: left;
}
.video-tableBox .reportBox .reportList > ul > li label {
  cursor: pointer;
}
.video-tableBox .reportBox .reportList > ul > li .iradio_flat-blue {
  margin-right: 5px;
}
.video-tableBox .reportBox .reportList .submit-btn {
  min-width: 110px;
  display: block;
  margin: 0 auto;
  margin-top: 12px;
  background: #03acef;
  height: 28px;
  line-height: 28px;
  border-radius: 4px;
  text-align: center;
  color: white;
}
.video-tableBox .pag-box {
  padding: 15px 10px;
}
.video-tableBox .pag-box .blueBtn {
  margin-left: 15px;
}
.vi-tables .describeTd {
  text-align: left;
}
.vi-tables .controlTd {
  text-align: right;
}
.vi-tables tr td {
  padding: 15px 10px;
}
#uploadVideo .modal-dialog {
  width: 530px;
}
#uploadVideo .modal-body {
  text-align: left;
}
#uploadVideo .pop-close {
  float: right;
  margin: -1px 20px 0 0;
}
#uploadVideo .iradio_flat-blue {
  margin-right: 5px;
}
.redStar {
  display: inline-block;
  vertical-align: middle;
  color: #f54545;
}
.video-message {
  padding: 0 60px 20px;
}
.video-message > ul > li {
  margin-top: 25px;
}
.video-message > ul > li > p {
  margin-bottom: 12px;
}
.video-message > ul > li > p > label {
  margin-right: 45px;
  cursor: pointer;
}
.video-message > ul > li > p > label:last-child {
  margin-right: 0;
}
.video-message > ul > li .redStar {
  margin: 2px 3px 0 0;
}
.video-message > ul > li .name {
  font-size: 12px;
  color: #6c849c;
}
.video-message > ul > li .uninps {
  width: 100%;
}
.video-message .uploadBox {
  width: 100%;
  height: 125px;
  border: 2px dashed #d4e1e9;
  border-radius: 3px;
  text-align: center;
  background: #f9fafa;
}
.video-message .uploadBox .upload-btn {
  margin-top: 22px;
}
.video-message .uploadBox .p1 {
  margin-top: 15px;
  font-size: 14px;
  color: #283e53;
}
.video-message .uploadBox .p2 {
  margin-top: 4px;
  font-size: 12px;
  color: #6c849c;
}
.video-fileList > ul > li {
  margin-top: 20px;
}
.video-fileList .viName {
  display: inline-block;
  vertical-align: middle;
  max-width: 80%;
  font-size: 14px;
  color: #283e53;
}
.video-fileList .percent {
  display: inline-block;
  vertical-align: middle;
  margin-left: 5px;
  color: #ffac2a;
}
.video-fileList .icon-line-del {
  float: right;
  display: inline-block;
  vertical-align: middle;
  margin-top: -4px;
  cursor: pointer;
  color: #8fa1b5;
}
.video-fileList .out-progress {
  margin-top: 5px;
  position: relative;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background: #b3e6fa;
}
.video-fileList .out-progress .in-progress {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 50%;
  height: 4px;
  border-radius: 4px;
  background: #03acef;
}
.videoTip {
  position: fixed;
  right: 7%;
  bottom: 10%;
  min-width: 385px;
  padding: 0 20px;
  height: 50px;
  line-height: 50px;
  background: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-size: 16px;
  color: #283e53;
}
.videoTip .icon-tip {
  margin-right: 3px;
  margin-top: -3px;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  background: url("../images/icon-shibai.png") no-repeat center center;
}
.videoTip .state {
  display: inline-block;
  vertical-align: middle;
  margin-left: 8px;
  font-size: 14px;
  color: #f44336;
}
.videoTip .btnWrap {
  float: right;
}
.videoTip .btnWrap .pop-open {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  background: url("../images/pop-open.png") no-repeat center center;
}
.videoTip .btnWrap .close-tip {
  display: inline-block;
  vertical-align: middle;
  width: 18px;
  height: 18px;
  background: url("../images/close.png") no-repeat center center;
}
.videoTip .btnWrap a {
  margin-left: 20px;
}
.videoTip .btnWrap a:first-child {
  margin-left: 0;
}
.videoTip.successTip .icon-tip {
  background: url("../images/icon-wancheng.png") no-repeat center center;
}
.videoTip.successTip .state {
  color: #ffac2a;
}
.videoTip.ingTip .icon-tip {
  width: 12px;
  height: 12px;
  background: #4caf50;
  border-radius: 50%;
}
.videoTip.ingTip .state {
  color: #ffac2a;
}
